import { OneSignal } from '@ionic-native/oneSignal';
import { HttpClient } from '@angular/common/http';
import { Component } from '@angular/core';
import { Storage } from '@ionic/storage';
import { NavController, NavParams, LoadingController, ToastController } from 'ionic-angular';


@Component({
  selector: 'page-register',
  templateUrl: 'register.html',
})
export class RegisterPage {
  name: String;
  email: String;
  password: String;
  phone: String;
  onesignal: String;

  constructor(public navCtrl: NavController, public navParams: NavParams, public http: HttpClient, public loadingCtrl: LoadingController, public storage: Storage, public toastCtrl: ToastController, private oneSignal: OneSignal) {
    this.email = this.navParams.get('email');
    this.oneSignal.getIds().then((data) => {
    	this.onesignal = data.userId;
    });
  }

  register() {
    let loader = this.loadingCtrl.create({
      spinner: 'dots',
      content: 'Creating Account...'
    });
    loader.present();
    let toast = this.toastCtrl.create({
      message: 'Email is already used, try signing in.',
      position: 'bottom',
      duration: 3000
    });

    this.http.post('https://realm-glory.herokuapp.com/api/auth/user', {
      name: this.name,
      email: this.email,
      password: this.password,
      phone: this.phone,
      onesignal: this.onesignal
    }).subscribe((data) => {
      loader.dismiss();
      if (data['status'] === 'success') {
        this.storage.set('user', data['data']);
        this.navCtrl.setRoot('TabsPage');
      } else {
        // alert(data['msg']);
        toast.present();
      }
    }, (err) => {
      loader.dismiss();
      console.log(JSON.stringify(err));
    })
  }

}


